import React from "react";
import "./style.scss";


const menu = require("./../../menu.json");


const getrandomColor = function () {
  let r = Math.floor(Math.random() * 255);
  let g = Math.floor(Math.random() * 255);
  let b = Math.floor(Math.random() * 255);
  return 'rgba(' + r + ',' + g + ',' + b + ',0.8)';
}

class Home extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      onScrollKey: 0
    };
  }

  onScroll = (e) => {
    let isFirefox = false;
    if (navigator.userAgent.indexOf("Firefoox") !== -1) {
      isFirefox = true;
    }
    let deltaY = e.deltaY;
    if (isFirefox) {
      deltaY = -deltaY;
    }
    if (deltaY > 0) {
      if (this.state.onScrollKey !== 0) {        
        this.setState({
          onScrollKey:this.state.onScrollKey - 1
        })
      }
    } else {
      if (this.state.onScrollKey < (menu.length - 1)) {        
        this.setState({
          onScrollKey:this.state.onScrollKey + 1
        })
      }
    }    
  }
  componentWillMount() {
    if(typeof document !==  "undefined"){
      document.addEventListener("mousewheel", this.onScroll);
    }
  }

  render() {
    return (
      <div
        className="home"
      >
        {
          menu.map((item, key) => {
            return (
              <div
                key={key}
                className="home-item"
                style={{
                  backgroundColor: `${getrandomColor()}`,
                  display: `${key === this.state.onScrollKey ? "block" : "none"}`,
                }}
              >
                <div>请滚动鼠标</div>
                <div>这是第{key + 1}页</div>
              </div>
            )
          })
        }
      </div>
    )
  }
}
export default Home
